ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  padding: 0;
  margin: 0;
}

button {
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

a {
  display: block;
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

input {
  outline: none;
}

fieldset {
  padding: 0;
  border: none;
  margin: 0;
}

@font-face {
  font-display: swap;
  font-family: Bluu-Next;
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/BluuNext-Titling.woff2") format("woff2");
}
.visually-hidden:not(:focus, :active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  margin: -1px;
  clip: rect(0 0 0 0);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 10px;
  scroll-behavior: smooth;
}

body {
  background: #e1d4c9;
  color: #403f3d;
  font-family: Bluu-Next, sans-serif;
  font-size: 2rem;
  font-weight: 600;
  letter-spacing: normal;
  line-height: 1.05;
  text-transform: none;
}

img {
  max-width: 100%;
  height: auto;
}

h1 {
  font-size: 6rem;
}
@media (max-width: 768px) {
  h1 {
    font-size: 4rem;
  }
}

h2 {
  font-size: 3rem;
}
@media (max-width: 480px) {
  h2 {
    font-size: 1.8rem;
  }
}

p {
  font-size: 2rem;
}
@media (max-width: 480px) {
  p {
    font-size: 1.4rem;
  }
}

.main {
  display: flex;
  overflow: auto;
  height: 80vh;
  align-items: center;
  padding: 0 60px;
  column-gap: 50px;
}
@media (max-width: 1195px) {
  .main {
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media (max-width: 768px) {
  .main {
    height: 84vh;
    padding: 0 20px;
  }
}
@media (max-width: 480px) {
  .main {
    padding: 0 10px;
  }
}
@media screen and (height <= 1000px) {
  .main {
    padding: 10px 20px;
    row-gap: 10px;
  }
}

.link {
  font-size: 3rem;
}
@media (max-width: 768px) {
  .link {
    font-size: 2rem;
  }
}